$dark-color:  #5a1d1af5;
$light-color: #eee6e6;

.library {
  margin: auto;
  width: 44vw;
  border: 1px solid transparent;
  border-radius: 0 0 50px 50px;

  &__element {
    margin-bottom: 6vh;
    padding: 2vh;
    text-align: center;
    list-style-type: none;

    &:first-child {
      margin-top: 6vh;
    }
  }
}

.itemLink {
  display: block;
  font-size: 24px;
  color: $light-color;
  text-decoration: none;
  text-transform: capitalize;
}

.title {
  margin: 5vh 0;
  padding: 15px;
  text-align: center;

  &--small {
    margin: 0;
  }
}

.download-link {
  width: fit-content;
  margin: 5vh auto;
  display: flex;
  font-size: 20px;
  background-color: transparent;

  &--small {
    margin: 5vh auto;
  }
}

.player {
  margin: 5vh auto;
  display: flex;
  background-color: transparent;

  &--small {
    margin: 5vh auto;
  }
}
